.edu-progress,
.edu-progress__bar {
	width: 100%;
	height: 5px;
	appearance: none;
	border: none;
}
.edu-progress--fixed {
	position: fixed;
	z-index: 100000;
}
.edu-progress {
	background-color: $color_red;  // progress BAR in FF
	color: $color_red; // progress BAR in IE10
	&::-webkit-progress-bar { // progress BAR in FF
		background-color: $color_red;
	}
	&::-webkit-progress-value { // progress VALUE in FF
		position: relative;
		background-color: $color_blue;
		transition: all .2s ease;
	}
	&::-moz-progress-bar { // progress VALUE in FF
		position: relative;
		background-color: $color_blue;
		transition: all .2s ease;
	}
}
// fallback
.edu-progress__bar {
	background-color: $color_red;
}
.edu-progress__value {
	display: block;
	height: 5px;
	background-color: $color_blue;
	transition: all .2s ease;
}