.edu-pagination {
  margin: ($bl*2) 0 0 0;
  text-align: center;

  ul {
    display: inline-block;
  }

  li {
    display: inline;

    &.current > * {
      color: $color_mute;
    }
  }

  .edu-pagination__prev > * {
    padding-left: 0;
  }

  .edu-pagination__next > * {
    padding-right: 0;
  }

  .edu-pagination__all {
    &:before {
      content: '|';
      float: left;
      color: $color_mute;
      padding-left: $bl;
    }
  }

  a, span {
    float: left;
    padding: 0 $bl;
    font-weight: 500;
  }

  a:hover {
    color: $color_red;
  }
}