/** VARIABLES 
===================================*/
$controls: 'images/controls.png';
$loader:   'images/bx_loader.gif';

/** RESET AND LAYOUT
===================================*/

.bx-wrapper {
  position: relative;
  margin: 0 auto;
  padding: 0;
  *zoom: 1;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  img {
    max-width: 100%;
    display: block;
  }
}

.bxslider {
  margin: 0;
  padding: 0;
}

ul.bxslider {
  list-style: none;
}

.bx-viewport {
  /*fix other elements on the page moving (on Chrome)*/
  -webkit-transform: translatez(0);
}

/** THEME
===================================*/


/* LOADER */
.bx-wrapper .bx-loading {
  min-height: 50px;
  background: url($loader) center center no-repeat #fff;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2000;
}

/* PAGER */
.bx-wrapper {
  .bx-pager {
    text-align: center;
    font-size: .85em;
    font-family: Arial;
    font-weight: bold;
    color: #666;
    padding-top: 20px;
    &.bx-default-pager a {
      background: #666;
      text-indent: -9999px;
      display: block;
      width: 10px;
      height: 10px;
      margin: 0 5px;
      outline: 0;
      -moz-border-radius: 5px;
      -webkit-border-radius: 5px;
      border-radius: 5px;
      &:hover,
      &.active,
      &:focus {
        background: #000;
      }
    }
  }
  .bx-pager-item,
  .bx-controls-auto .bx-controls-auto-item {
    display: inline-block;
    *zoom: 1;
    *display: inline;
  }
  .bx-pager-item {
    font-size: 0;
    line-height: 0;
  }
}

/* DIRECTION CONTROLS (NEXT / PREV) */  
.bx-wrapper {
  .bx-prev {
    left: 10px;
    background: url($controls) no-repeat 0 -32px;
    &:hover,
    &:focus {
      background-position: 0 0;
    }
  }
  .bx-next {
    right: 10px;
    background: url($controls) no-repeat -43px -32px;
    &:hover,
    &:focus {
      background-position: -43px 0;
    }
  }
  .bx-controls-direction a {
    position: absolute;
    top: 50%;
    margin-top: -16px;
    outline: 0;
    width: 32px;
    height: 32px;
    text-indent: -9999px;
    z-index: 9999;
    &.disabled {
      display: none;
    }
  }
}
