.edu-dialog {
  position: relative;
	margin: 50% ($bl*2) 0 ($bl*2);
	padding: ($bl*2);
	background: #fff;
	box-shadow: 0 0 30px rgba($color_text, .3);
	border-bottom: 5px solid $color_line;
  transition: transform .1s ease;
  z-index: 200;
	transform: translateY(-50%) scale(.3);
  .edu-dialog_overlay--visible & {
  	transform: translateY(-50%) scale(1);
  }
}
.edu-dialog__wrapper {
	position: relative;
	margin: 0 auto;
	max-width: 640px;
}
.edu-dialog__overlay {
	position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  transition: background .1s ease;
  background: rgba($color_text,0);
}
.edu-dialog_overlay--visible {
  background: rgba($color_text,.2);
}
.edu-dialog_overlay--hidden {
	position: absolute; 
	overflow: hidden; 
	clip: rect(0 0 0 0); 
	height: 1px; width: 1px; 
	margin: -1px; padding: 0; border: 0; 
}
.edu-dialog__header {
	margin-bottom: $bl;
}
.edu-dialog__heading {
	font-size: 24px;
	font-weight: 700;
	line-height: 1;
}
.edu-dialog__message {
	padding: 0;
}
.edu-dialog__buttons {
	margin-top: $bl;
}