.edu-form__heading {
  @extend %edu-section-heading;
}

//////////////////////////
// Placeholder
//////////////////////////
::-webkit-input-placeholder {
  color: $color_placeholder;
}
:-moz-placeholder {
  color: $color_placeholder;
  opacity: 1;
}
::-moz-placeholder {
  color: $color_placeholder;
  opacity: 1;
}
:-ms-input-placeholder {
  color: $color_placeholder;
}
.placeholder {
  color: $color_placeholder;
}

//////////////////////////
// Form Elements
//////////////////////////
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
input[type="tel"],
input[type="url"],
select,
textarea {
	border: 1px solid $color_form;
	border-radius: 3px;
	margin-bottom: ($bl/2);
	max-width: 100%;
	&:focus {
		border-color: $color_dimmed;
	}
}
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
input[type="tel"],
input[type="url"],
input[type="submit"],
input[type="reset"] {
	padding: 0 .471em; //8px
}
input[type="search"] {
	appearance: none;
}
select {
	padding: 0 .235em; //4px
}
select[multiple],
select[size] {
    height: auto;
}
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
input[type="tel"],
input[type="url"],
input[type="submit"],
input[type="reset"] {
	-webkit-appearance: none;
}

//////////////////////////
// select
//////////////////////////
select {
	background: $color_box;
	background: linear-gradient($color_fond,$color_box);
}
select::-ms-expand {
	border: 0;
	background: transparent;
}
@-moz-document url-prefix() {
	select {
		padding-top: 3px; //FF vertical centering
	}
}

//////////////////////////
// labels
//////////////////////////

// This label sits above the form field
//
label.edu-label--top {
	display: block;
}

// This label has a helper tooltip
// and is marked with an info icon
// @deprecated
.edu-label--has-tooltip {
	&::after {
		content: '';
		display: inline-block;
		background: url('../img/svg-icons/edu-info.svg') no-repeat;
		background-size: 12px 12px;
		width: 12px;
		height: 12px;
		margin: 2px 0 0 4px;
		cursor: pointer;
		vertical-align: baseline;
		opacity: .25;
	}
	&.edu-label--top {
		display: inline-block;
	}
}

//////////////////////////
// buttons
//////////////////////////
.btn,
input[type="submit"]  {
	background-color: $color_link;
	color: #fff;
	font-weight: 500;
	border: 0;
	border-radius: 3px;
	padding: 0 12px;
	line-height: 28px;
	transition: all .1s ease;
	margin-bottom: ($bl/2);
	&:hover,
	&:focus {
		background-color: $color_red;
		box-shadow: inset 0 -2px 0 0 shade($color_red,30);
	}
	&:active {
		background-color: $color_text;
		box-shadow: inset 0 2px 0 0 shade($color_text,30);
	}
}
input[type="reset"]  {
	background-color: transparent;
	color: $color_red;
	border: 1px solid $color_form;
	border-radius: 3px;
	padding: 0 12px;
	font-weight: 500;
	transition: all .1s ease;
	margin-bottom: ($bl/2);
	&:hover,
	&:focus {
		border: 1px solid $color_red;
	}
	&:active {
		color: $color_text;
		border: 1px solid $color_text;
	}
}
a.btn {
	display: inline-block;
	white-space: nowrap;

	& + a.btn {
		margin-left: 2px;
	}
}
input[type="submit"],
input[type="reset"] {
	//vertical-align: middle; // TODO - check in FF
}
input[type="submit"] {
	text-transform: uppercase;
	font-weight: 700;
}

// margin between adjacent input and submit button
input + input[type="submit"] {
	margin-left: 6px;
}

.fieldset--basic {
	border: 0;
	padding: 0;
	margin: 0 0 $bl 0;
}
.fieldset-heading {
	font-family: GravurCondensed, sans-serif;
	font-size: 24px;
	font-weight: 700;
	text-transform: uppercase;
	margin: 0 0 $bl 0;
}

//////////////////////////
// single line forms
//////////////////////////
.single-line-form {
	display: table;
	width: 100%;
	margin-right: -($bl);
}
.single-line-input {
	display: table-cell;
	vertical-align: top;
	width: 100%;
	padding-right: $bl;
	input, select {
		width: 100%;
	}
}
input.input--single-line-submit {
	margin-left: 12px;
}
.single-line-submit {
	display: table-cell;
	float: right;
}

//////////////////////////
// radio pill buttons
//////////////////////////
.edu-pillbtn {
	overflow: hidden;
	header {
		text-transform: uppercase;
		color: $color_dimmed;
		margin: 0 0 3px 0;
	}
	input {
		display: none;
	}
	label {
		float: left;
		display: block;
		line-height: 28px;
		height: 28px;
		font-size: 14px;
		text-align: center;
		padding: 0 12px;
		background: $color_fond;
		background: linear-gradient($color_fond,$color_box);
		border-right: 1px solid #fff;
		border-bottom: 2px solid shade($color_line,10);
		cursor: pointer;
		&:first-of-type {
			border-top-left-radius: 6px;
			border-bottom-left-radius: 6px;
		}
		&:last-of-type {
			border-top-right-radius: 6px;
			border-bottom-right-radius: 6px;
			border-right: 0;
		}
		&:hover {
			border-bottom-color: $color_link;
		}
	}
	input:checked + label {
		border-bottom-color: shade($color_green,15);
		background: tint($color_green,15%);
		background: linear-gradient($color_green,tint($color_green,15));
		color: #fff;
		cursor: default;
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) {
	.btn--huge {
		padding: 12px 24px;
	}
	input.input--huge {
		padding: 0 12px;
		height: 42px;
		font-size: 20px;
	}
	.btn--huge,
	input[type="submit"].input--huge {
		border-radius: 6px;
		&:hover {
			box-shadow: inset 0 -4px 0 0 shade($color_red,30);
		}
		&:active {
			box-shadow: inset 0 4px 0 0 shade($color_text,30);
		}
	}
}
