.edu-comment {
  @extend .edu-well;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: -9px;
    top: 16px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 9px 8px 0;
    border-color: transparent #ffffff transparent transparent;
  }
}

.edu-comment__header {

}

.edu-comment__meta {
  color: $color_dimmed;
  display: inline-block;
  border-bottom: 2px solid $color_line;
  margin: 0 0 $bl 0;
}

.edu-comment__meta__author {
  color: $color_text;
  font-weight: bold;
}

.edu-comment__meta__date {
  text-transform: none;
}

.edu-comment__content {
  :last-child {
    margin-bottom: 0;
  }
}