.tx-stwjqueryfeedback-pi1 {

	.slide-out-div {
		margin-top: 5px;
		background: $color_fond;
		border: 5px solid #fff;
		border-left: 0;
		box-shadow: 0 1px 5px rgba($color_text,.5);
		width: 250px;
		z-index: 100;

		.inner {
			padding: 20px;
			width: 240px;
			margin-top: -5px;
		}

		h3 {
			font-weight: bold;
			font-size: 22px;
		}

		label {
			display: block;
			width: 100%;
			margin: 12px 0 0;
		}

		input, textarea {
			margin: 6px 0 0;
		}

		input {
			&.tx-stwjqueryfeedback-captcha-input  {
				margin-left:80px;
			}
		}

		textarea {
			width: 100%;
			height: 130px;
		}

		fieldset {
			border: none;
			margin: 0;
			padding: 0;

			br {
				display: none;
			}

			input {
				display: block;
				width: 100%;
			}

			input#submit {
				width: auto;
				margin: 12px auto 0;
			}
		}
	}

	a.handle,
	a.handle[style] {
		width: 30px;
		height: 146px;
		display: block;
		background: url('../img/typo3/tx-stwjqueryfeedback-pullout.png') no-repeat;
		background-size: 30px 146px;
		display: block;
    text-indent: -99999px;
    outline: none;
    position: absolute;
    top: 7px !important;
    right: -35px !important;
    z-index: 100;
	}
}

.tx-stwjqueryfeedback-captcha-error {
	color:#FFFFFF;
	background-color: #CC0000;
	margin-left:80px;
	width: 140px;
	padding:3px;
	clear:both;
	float:none;
	display:block;
}

.tx-stwjqueryfeedback-required-mark {
	color: #CC0000;
}
