.edu-teaser-list__heading {
  @extend %edu-section-heading;
}

.edu-teaser-list__filter {
  border-top: 1px dotted $color_line;
  padding: ($bl/2) 0 ($bl/2) 0;
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
}

.edu-teaser-list__filter__el {
  padding: 0 $bl 0 0;
  color: $color_dimmed;

  select {
    border: 0;
    background: transparent;
    appearance: none;
    color: $color_link;
    font-weight: bold;
    padding: 0;
    border-radius: 0;
  }
}

.edu-teaser {
  background-color: #fff;
  padding: $bl;
  margin: 0 0 $bl 0;
  border-bottom: 5px solid $color_box;
}

.edu-teaser__source {
  color: $color_box;
  margin: ($bl) 0 (-$bl / 2) 0;
  text-align: right;
  transition: color .1s ease;

  .edu-module-name {
    display: block;
  }

  .edu-teaser:hover & {
    color: $color_mute;
  }
}

.edu-teaser__img {
  img {
    width: 100%;
  }
}

.edu-teaser__kicker {
  color: $color_dimmed;

  > *:not(:first-child)::before {
    content: '  ';
  }

  time {
    text-transform: none;
  }
}

.edu-teaser__meta {
  th {
    padding-right: $bl;
    white-space: nowrap;
  }
}

.edu-teaser__heading {
  margin: 0 0 ($bl) 0;
  font-size: 24px;
  font-weight: 700;

  aside & {
    margin: 0 0 ($bl/2) 0;
    font-size: 18px;
  }
}

.edu-teaser__content {
  @extend .small;
}

.edu-meta-table {
  line-height: 21px;

  th {
    white-space: nowrap;
  }

  th, td {
    padding: 4px $bl 4px 0;

    &:last-child {
      padding-right: 0;
    }
  }
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: $bp_s) {
  .edu-teaser__img {
    margin: 0 0 $bl 0;
  }
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)), print {
  figure.edu-teaser__img {
    float: left;
    margin: 0 ($bl*2) 0 0;
    max-width: 25%;
  }
}
