.edu-list-search {
  background: #fff;
  padding: $bl;
}

.edu-list-search__select,
.edu-list-search__input {
  width: 100%;
}

.edu-list-search__label,
.edu-list-search__legend {
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.edu-list-search__label__link {
  font-weight: normal;
}

.edu-list-search__el,
.edu-list-search__fieldset {
  margin: 0 0 ($bl/2) 0;
  padding: 0;
  border: 0;

  :last-child {
    margin-bottom: 0;
  }
}

.edu-list-search__el--half {
  width: 50%;
  float: left;

  &:first-child {
    padding-right: ($bl/2);
  }

  &:last-child {
    padding-left: ($bl/2);
  }
}
