//////////////////////////
// general header stuff
//////////////////////////
.edu-header {
	padding: 14px 0 9px 0;
	background: #fff;
	border-top: 5px solid $color_red;
	box-shadow: 0 2px 5px rgba(#222,.2);
	transition: all .1s ease;
	position: relative;
	z-index: 20;
}
.edu-header--open-menu {
	box-shadow: none;
	border-top: 5px solid $color_blue;
	background-color: $color_text;
	.edu-module-title__part {
		color: #fff;
	}
}
.edu-header__logo {
	&:hover {
		text-decoration: none;
	}
	.edu-module-title__part {
		font-size: 30px;
		line-height: ($bl*3);
	}
}

//////////////////////////
// buttons
/////////////////////////
.edu-menu-btn {
	display: inline-block;
	border: 0;
	padding: 0;
	margin: 0 0 0 24px;
	height: ($bl*3);
	vertical-align: top;
	line-height: ($bl*3);
	background: none;
	color: $color_mute;
	font-size: 18px;
	font-weight: 500;
	text-transform: uppercase;
	transition: color .1s ease;
	.touch &:focus {
		outline: none;
	}

	// menu buttons :hover
	&:hover {
		text-decoration: none;
		color: $color_red;
		.edu-menu-btn__icon { fill: $color_red; }
	}

	// menu buttons :active
	&:active {
		color: $color_text;
		outline: none;
		.edu-menu-btn__icon { fill: $color_text;	}
	}

	// menu buttons on header--open-menu
	.edu-header--open-menu & {
		color: $color_mute;
		.edu-menu-btn__icon { fill: $color_mute; }
		&:hover {
			color: $color_blue;
			.edu-menu-btn__icon { fill: $color_blue; }
		}
	}
}

.edu-menu-btn__label {
	margin-right: 4px;
}

.edu-menu-btn--close {
	width: 100%;
	height: ($bl*4);
	margin: 0;
}

.edu-menu-btn__icon {
	vertical-align: text-bottom;
	fill: $color_mute;
	transition: fill .1s ease;
}

.edu-menu-btn--nav {
	color: $color_blue;
}
.edu-menu-btn__icon--nav {
	fill: $color_blue;
}

.edu-menu-btn--active {
	color: $color_text !important;
	.edu-menu-btn__icon {
		fill: $color_text !important;
	}

	// active menu button on header--open-menu
	.edu-header--open-menu & {
		color: #fff !important;
		.edu-menu-btn__icon {
			fill: #fff !important;
		}
	}
}

//////////////////////////
// browsehappy
/////////////////////////
.browsehappy { //TODO
	position: relative;
	z-index: 20;
	padding: ($bl);
	text-align: center;
	background: $color_red;
	color: #fff;
	a {
		color: #fff;
		font-weight: 700;
		text-decoration: underline;
	}
}

//////////////////////////
// menus
/////////////////////////
.edu-header-menu {
	position: absolute;
	z-index: 11;
	width: 100%;
	background: #fff;
	box-shadow: 0 2px 5px rgba(#222,.2);
	transition: transform .25s ease;
}
.edu-header-menu--hidden {
	box-shadow: none;
	z-index: 10;
}
.no-csstransforms .edu-header-menu--hidden {
	height: 1px;
	width: 1px;
	overflow: hidden;
	clip: rect(1px 1px 1px 1px);
	&:focus {// show menu on focus (accesibility)
		height: inherit !important;
		width: inherit !important;
		overflow: inherit !important;
		clip: auto; 
	}
}
.csstransforms {
	.edu-header-menu--hidden {
		transform: translateY(-100%);
		&:focus {
			transform: translateY(0); // show menu on focus (accesibility)
		}
	}
}

.edu-header-menu__footer {
	border-top: 1px solid $color_line;
	text-align: center;
}

//////////////////////////
// module navigation
/////////////////////////
.edu-module-nav {
	overflow: hidden;
	.edu-module-title__part {
		font-size: 24px;
		word-break: break-all;
		width: 100%;
	}
}
.edu-module-nav__list {
	.flexbox & {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: -1px; 
	}
}
.edu-module-nav__item {
	.flexbox & {
		border-bottom: 1px solid $color_line;
		border-right: 1px solid $color_line;
	}
	a {
		display: block;
		padding: ($bl*3) ($bl);
		&:hover {
			text-decoration: none;
			.edu-module-title__part--right {
				color: $color_blue;
			}
		}
	}
}
.edu-module-nav__item--first {
	width: 100%;
	a:hover .edu-module-title__part--right {
		color: $color_red;
	}
	.edu-module-title__part {
		font-size: 30px;
	}
}
.edu-module-nav__item--active {
	.edu-module-title:after {
		content: '';
		display: inline-block;
		width: 9px;
		height: 9px;
		border-radius: 10px;
		border: 2px solid $color_blue;
		margin: 0 0 0 3px;
		vertical-align: top;
	}
}
.edu-nav-module__teaser {
	margin: 0;
	font-size: 15px;
	line-height: 18px; 
	color: $color_mute;
}

//////////////////////////
// search
/////////////////////////
.edu-search__panel {
	padding-top: ($bl*8);
	padding-bottom: ($bl*8);
}
.edu-search__fieldset {
	max-width: 600px;
	margin: 0 auto;
}
.edu-search__option {
	font-size: 15px;
}

//////////////////////////
// login
/////////////////////////
.edu-login-form__panel,
.edu-register-form__panel {
	padding-top: ($bl*2);
	padding-bottom: ($bl*2);
}
.edu-login-form__input,
.edu-register-form__input {
	width: 200px;
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: $bp_s) {
	.edu-header__logo {
		.edu-module-title__part {
			font-size: 24px;
			line-height: 28px;
		}
	}
	.edu-header {
		.edu-menu-btn {
			margin-top: -3px;
			height: 28px;
			line-height: 28px;
		}
	}
	.edu-module-nav {
		.edu-module-title__part {
			font-size: 20px;
		}
	}
	.edu-module-nav__item {
		.flexbox & {
			border-right: 0;
		}
		a {
			padding: ($bl*1) 0;
		}
	}
	.edu-search__panel {
		padding-top: ($bl*4);
		padding-bottom: ($bl*4 );
	}
	.edu-login-form__panel {
		border-bottom: 1px solid $color_line;
	}
}


//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) {
	.edu-module-nav__item--first {
		text-align: center;
	}
	.edu-search__option {
		display: inline-block;
		padding: 0 $bl 0 0;

		&:last-child {
			padding: 0;
		}
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480 < 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) and (max-width: $bp_m) {
	.edu-module-nav__item {
		&:nth-child(2n+1) {
			border-right: 0;
			a {
				padding-right: 0;
			}
		}
		&:nth-child(2n+2) {
			clear: left;
			a {
				padding-left: 0;
			}
		}
	}
	.edu-login-form__panel {
		border-bottom: 1px solid $color_line;
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_m + 1)) {
	.flexbox .edu-login__panel {
		display: flex;
	}
	.edu-login-form__panel {
		border-right: 1px solid $color_line;
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 768 < 960
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_m + 1)) and (max-width: $bp_l) {
	.edu-module-nav__item {
		&:nth-child(3n+1) {
			border-right: 0;
			a {
				padding-right: 0;
			}
		}
		&:nth-child(3n+2) {
			clear: left;
			a {
				padding-left: 0;
			}
		}
	}
	.edu-login-form__panel,
	.edu-register-form__panel {
		padding-top: ($bl*6);
		padding-bottom: ($bl*6);
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 960
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_l + 1)) {
	.edu-module-nav__item {
		&:nth-child(4n+1) {
			border-right: 0;
			a {
				padding-right: 0;
			}
		}
		&:nth-child(4n+2) {
			clear: left;
			a {
				padding-left: 0;
			}
		}
	}
	.edu-login-form__panel,
	.edu-register-form__panel {
		padding-top: ($bl*8);
		padding-bottom: ($bl*8);
	}
}
