
// Clear floats after csc-textpic and after csc-textpic-imagerow
.csc-textpic,
.csc-textpic .csc-textpic-imagerow {
  @extend %clearfix;
}

.csc-textpic-imagewrap {
  margin-top: ($bl/2);
  max-width: 50%;

  .csc-textpic-intext-left & {
    float: left;
    margin-right: ($bl*2);
  }

  .csc-textpic-intext-right & {
    float: right;
    margin-left: ($bl*2);
  }

  img {
    width: 100%;
    height: 100%;
  }
}