.hero {
	background-color: $color_red;
}
.hero--bgimg {
	background-size: cover;
	background-position: center bottom;
}
.hero__header {
	color: #fff;
	font-size: 16px;
	line-height: 20px;
	font-weight: 400;
	text-shadow: 0 1px 3px rgba($color_text, .57);
}
.hero__header__em {
	display: block;
	font-size: 28px;
	line-height: 1;
	font-family: GravurCondensed;
	letter-spacing: .01em;
	font-weight: 400;
	text-transform: uppercase;
	margin-left: ($bl*2);
	margin-top: ($bl/2);
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) {
	.hero__header {
		font-size: 24px;
		line-height: 28px;
		font-weight: 300;
		max-width: 66.6666%;
	}
	.hero__header__em {
		font-size: 42px;
		margin-left: ($bl*3);
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_m + 1)) {
	.hero__header {
		font-size: 28px;
		line-height: 32px;
	}
	.hero__header__em {
		font-size: 48px;
		margin-top: $bl;
	}
}