.edu-footer {
	padding-top: ($bl*4);
	padding-bottom: ($bl*2);
	color: $color_dimmed;
	font-size: 15px;
}
.edu-footer-menu {
	border-bottom: 1px solid $color_line;
	padding-bottom: ($bl*1);
	margin-bottom: ($bl*2);
	a {
		color: $color_dimmed;
		&:hover {
			border-bottom: 1px solid $color_mute;
		}
	}
}
.edu-footer-logos {
	font-size: 13px;
}
.edu-footer-logos__header {
	margin-bottom: ($bl/2);
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: $bp_s) {
	.edu-footer {
		font-size: 13px;
		text-align: center;
	}
	.edu-footer-menu {
		border-top: 1px solid $color_line;
		padding-top: ($bl*1);
	}
	.edu-footer-menu__item {
		display: block;
	}
	.edu-footer-logos {
		img {
			display: block;
			margin: 0 auto;
		}
	}
	.edu-footer-logos--sponsors {
		padding-top: ($bl);
		img {
			margin: 0 auto ($bl*2) auto;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	.edu-footer-logos--gei {
		border-bottom: 1px solid $color_line;
		padding-bottom: ($bl*2);
		.edu-footer-logos__header {
			display: none;
		}
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) {
	.edu-footer-menu__item {
		margin-right: ($bl/2);
	}
	.edu-footer-logos--sponsors {
		img {
			margin-right: ($bl*2);
			margin-bottom: ($bl*1);
			vertical-align: top;
		}
	}
	.edu-footer-logos--gei {
		float: right;
	}
}