// .preload will be removed by js on page load,
// prevents transitions from playing on page load
.preload * {
  transition: none !important;
}

// .edu-module is the container for all edumeres modules,
// it prevents the modules from interfering with the surrounding ui
.edu-module {
	position: relative;
	z-index: 0;
}

// .edu-main is a content wrapper for the main content area
.edu-main {
	padding-top: ($bl*3);
}


textarea.txa {
   resize: none;
    width:100% !important;
}

