caption {
	caption-side: bottom;
	text-align: left;
}
th, td {
	text-align: left;
	vertical-align: top;
}
.table {
	table {
		width: 100%;
	}
	caption {
		margin: ($bl/2) 0 0 0;
		font-size: 13px;
		color: $color_mute;
	}
	th, td {
		padding: ($bl/2) ($bl*2) ($bl/2) ($bl/2);
		&:last-child {
			padding-right: ($bl/2);
		}
	}
	td {
		border-top: 1px solid $color_line;
		border-bottom: 1px solid $color_line;
	}
	th {
		background: #fff;
		font-weight: 700;
	}
}

.table--data {
	font-size: 14px;
  line-height: 18px;
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: $bp_m) {
	.table--overflow {
		overflow-y: hidden;
		overflow-x: scroll;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		-webkit-overflow-scrolling: touch;
		border: 1px solid $color_line;
		margin-bottom: ($bl*2);
		//box-shadow: inset -8px 0 10px -8px rgba($color_text, 0.3);
		table {
			margin-bottom: 0;
		}
		th,	td {
			white-space: nowrap;
		}
		caption {
			padding: 0 ($bl/2);
		}
	}
}
