//////////////////////////
// box model
/////////////////////////
*, *:before, *:after {
	box-sizing: border-box;
}

//////////////////////////
// body
//////////////////////////
html, body {
	background: $color_fond;
	color: $color_text;
}
html { height: 100%; }
body { min-height: 100%; }

//////////////////////////
// nav list reset
//////////////////////////
nav, menu {
	li, ul{
		margin: 0;
		list-style: none;
	}
	li {
		display: inline-block;
	}
}

//////////////////////////
// page centering
//////////////////////////
.page-margins {
	margin: 0 auto;
	padding-left: ($bl*2);
	padding-right: ($bl*2);
	max-width: $site_max_width;

	.page-margins {
		padding-left: 0;
		padding-right: 0;
	}
}
.page-full-width {
	max-width: none;
}

//////////////////////////
// responsive embeds
//////////////////////////
.embed {
	position: relative;
	height: 0;
	padding-bottom: 100%; // 1:1
	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
.embed-16-9 {
	padding-bottom: 56.25%; // 16:9
}
.embed-4-3 {
	padding-bottom: 75%; // 4:3
}
.embed-3-2 {
	padding-bottom: 66.66666%; // 3:2
}

//////////////////////////
// Vertical centering multiline
//////////////////////////
.vc-wrap {
	width: 100%;
	height: 100%;
	display: table;
}
.vc {
	display: table-cell;
	height: 100%;
	vertical-align: middle;
}

//////////////////////////
// single line elements
//////////////////////////
.nowrap {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

//////////////////////////
// grid
//////////////////////////
.container {
	@extend %clearfix;
}
.positioned {
	position: relative;
}
.col {
	position: relative;
	float: left;
	width: 100%;
}

.col-right {
	float: right;
}
.col-gtr {
	padding: 0 $bl;
}

.col-std-main,
.col-std-aside {
	@extend .col;
}

.col-std-aside {
	@extend .col-right;
}

.pdg-t { padding-top: ($bl*1);}
.pdg-b { padding-bottom: ($bl*1);}

.mrg-t { margin-top: ($bl*2);}
.mrg-b { margin-bottom: ($bl*2);}

.gtr-r { padding-right: ($bl*2); }
.gtr-l { padding-left: ($bl*2); }

//////////////////////////
// floats
//////////////////////////
.pull-left {
	float: left;
}
.pull-right {
	float: right;
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: $bp_s) {
	.page-margins {
		padding-left: ($bl);
		padding-right: ($bl);
	}
	.span-s1 { width: 25%;}
	.span-s2 { width: 50%;}
	.span-s3 { width: 75%;}

	.offset-s1  { margin-left: 25%; }
	.offset-s2  { margin-left: 50%; }
	.offset-s3  { margin-left: 75%; }

	.pdg-s1 { padding-top: ($bl*1); padding-bottom: ($bl*1);}
	.pdg-st { padding-top: ($bl*1);}
	.pdg-sb { padding-bottom: ($bl*1);}

	.mrg-st { margin-top: ($bl*2);}
	.mrg-sb { margin-bottom: ($bl*2);}
	
	.gtr-sb  { padding-left: $bl; padding-right: $bl; }
	.gtr-sr { padding-right: $bl; }
	.gtr-sl { padding-left: $bl; }

	.align-sr {	text-align: right; }
	.align-sc {	text-align: center; }

	.hide-s { display: none; }

	.col-std-aside--first {
		@extend .mrg-sb;
	}

	.col-std-aside--last {
		@extend .mrg-st;
	}
}

