//////////////////////////
// selection
//////////////////////////
::-moz-selection {
	background: $color_link;
	color: #fff;
}
::selection {
	background: $color_link;
	color: #fff;
}

//////////////////////////
// font handling
//////////////////////////
// better font smoothing 
* {
	-webkit-font-smoothing: antialiased; // webkit / blink 
	-moz-osx-font-smoothing: grayscale; // ff on osx
} 
nav * { -webkit-tap-highlight-color: rgba(0,0,0,0); } // iOS tap color

//////////////////////////
// fonts
//////////////////////////
body, input, select, textarea {
	font-family: Gesta, sans-serif;
	color: $color_text;
}

body {
	font-size: 17px;
	line-height: ($bl*2);
}

input, select, textarea {
	font-size: inherit;
	line-height: inherit;
}

//////////////////////////
// links
//////////////////////////
a {
	text-decoration: none;
	color: $color_link;
}
a.edu-link {
	&:hover {
		text-decoration: underline;
	}
}
a.edu-link--warn {
	color: $color_red;
}
a.edu-link--pdf {
	&::before {
		content: 'PDF';
		display: inline-block;
		margin: 0 4px 0 0;
		color: #fff;
		background: $color_red;
		border-radius: 3px;
		font-size: .8em;
		line-height: 1.2;
		padding: 0 3px;
		font-weight: 500;
	}
}

//////////////////////////
// reset
//////////////////////////
p,ul,ol,table,hr,figure,h1,h2,h3,h4,h5,h6 {
	font-weight: 400;
	font-style: normal;
	font-size: inherit;
	line-height: inherit;
	margin: 0;
}

//////////////////////////
// basic typography
//////////////////////////
time {
	text-transform: uppercase;
}
.edu-content {
	a {
		&:hover {
			text-decoration: underline;
		}
	}
	em {
		font-style: italic;
	}
	strong {
		font-weight: 700;
	}
	blockquote {
		font-style: italic;
	}
	p,ul,ol,table,hr {
		margin-top: 0;
		margin-bottom: ($bl*2);
	}
	figure {
		margin-top: 0;
		margin-bottom: ($bl*1);
	}
	h1,h2,h3,h4,h5,h6 {
		font-weight: 700;
		margin-top: ($bl*3);
		margin-bottom: ($bl*1);
		&:first-child {
			margin-top: 0;
		}
	}
	h1 {
		margin: 0 0 ($bl*2) 0;
		font-size: 34px;
		line-height: ($bl*3);
	}
	h2 {
		font-size: 24px;
		line-height: ($bl*2);
	}
	h3 {
		font-size: 20px;
		line-height: ($bl*2);
	}
	h4, h5, h6 {
		font-size: 16px;
		line-height: ($bl*2);
		margin: ($bl*2) 0 ($bl*1) 0;
	}
	hr {
		border: 0;
		height: 2px;
		background: $color_line;
		clear: both;

	}
	.edu-kicker {
		margin: 0;
		color: $color_mute;
		text-transform: uppercase;
	}
	.edu-well {
		background: #fff;
		padding: $bl;
		margin: 0 0 ($bl*2) 0;

		& > :last-child {
			margin-bottom: 0;
		}
	}
}

// a content link
//
a.edu-c-link {
	border-bottom: 1px solid $color_line;
	&:hover {
		border-color: $color_link;
	}
}

// a directional link with an arrow shape
//
// .edu-dir-link--back a link with an arrow left
// .edu-dir-link--forward a link with an arrow right
//
a.edu-dir-link {
	.icon {
		width: 10px;
		height: 10px;
		fill: $color_link;
	}
}

.edu-dir-link--back {
	.icon {
		margin-right: 3px;
	}
}

.edu-dir-link--forward {
	.icon {
		margin-left: 3px;
	}
}

// an extendable generic section heading
//
.edu-section-heading,
%edu-section-heading {
	font-size: 24px;
	font-weight: 700;
	margin: 0 0 ($bl*2) 0;
}

//////////////////////////
// classes
//////////////////////////
.align-right {
	text-align: right;
}
.align-center {
	text-align: center;
}
.mute {
	color: $color_mute;
}
.small {
	font-size: 15px;
	line-height: 20px;
}
.lead {
	font-size: 18px;
	line-height: 26px;
}

//////////////////////////
// badges
//////////////////////////
.edu-badge {
	display: inline-block;
	border-radius: 3px;
	padding: 0 4px;
	font-weight: 500;
	margin: 0 2px 2px 0
}

a.edu-badge {
	border: 0;
	
	&:active {
		background-color: $color_text;
		box-shadow: inset 0 2px 0 0 shade($color_text,30);
	}
}

.edu-badge-green {
	background: $color_green;
	color: #fff;
}

a.edu-badge-green {
	
	&:hover,
	&:focus {
		box-shadow: inset 0 -2px 0 0 shade($color_green,30);
	}
}


//////////////////////////
// module titles
//////////////////////////
.edu-module-title,
.edu-module-name {
	display: inline-block;
	font-family: GravurCondensed;
	font-weight: 700;
	letter-spacing: .01em;
}
.edu-module-name {
	line-height: 1;
}
.edu-module-title {
	font-size: 0; //remove whitespace
	line-height: 1;
}
a.edu-module-title:hover,
.edu-module-title a:hover { // links with module title
	text-decoration: none;
}
.edu-module-title__part {
	font-size: 16px;
	line-height: 1;
}
.edu-module-title__part--left,
.edu-module-title__part--right {
	color: $color_text;
	transition: color .1s ease;
}
.edu-module-title__part--separator {
	color: $color_mute;
}
.edu-module-title__part--blue {
	color: $color_blue;
}
.edu-module-title__part--red {
	color: $color_red;
}


//////////////////////////
// contenteditable
//////////////////////////
.edu-editable {
	border-bottom: 1px dashed $color_mute;

	&:focus {
		outline: 0;
		border-bottom: 1px dashed $color_text;
	}
}

//////////////////////////
// Tooltip
//////////////////////////
.edu-tooltip {
	text-indent: -10000em;
	overflow: hidden;
	width: 12px;
	height: 12px;
	display: inline-block;
	position: relative;

	&::after {
		position: absolute;
		top: 50%;
		left: 0;
		margin-top: -6px;
		content: '';
		display: block;
		background: url('../img/svg-icons/edu-info.svg') no-repeat;
		background-size: 12px 12px;
		width: 12px;
		height: 12px;
		cursor: pointer;
		vertical-align: baseline;
		opacity: .25;
	}
}
