nav a,
.btn {
	&:hover {
		text-decoration: none !important;
	}
}

// apply to lists to remove standard list style and margin / padding
//
.edu-nav-list {
	margin: 0;
	padding: 0;
	list-style: none;
}

// a horizontal navigation
//
.edu-h-nav {
	@extend %clearfix;
}

// a vertical navigation
//
.edu-v-nav {
	.edu-nav__item {
		display: block;
	}
}

// the global navigation of a single module
//
.edu-module-header-nav {
	.edu-nav__item {
		white-space: nowrap;
		float: left;
		position: relative;
	}
	.edu-nav-item--active {
		.edu-nav__item__a {
			color: #fff;
			background: $color_link;
		}
	}
	.edu-nav__item__a {
		display: block;
		padding: ($bl/2);
		font-weight: 500;
		color: $color_dimmed;
		&:hover {
			color: #fff;
			background: $color_red !important;
		}
	}
	.edu-subnav__list {
		display: none;
		position: absolute;
		top: 36px;
		z-index: 2000;
		background: $color_dark;
		max-width: 280px;
		min-width: 100%;
		font-size: 16px;
		border-radius: 0 0 3px 3px;
		padding: 0 0 4px 0;
		box-shadow: 0 3px 4px rgba(#222,.15), inset 0 -4px 0 0 shade($color_dark,30);
	}
	.edu-nav__item--has-subnav:hover {
		.edu-nav__item__a {
			color: #fff;
			background: $color_dark;
		}
		.edu-subnav__list {
			display: block;
		}
	}
	.edu-subnav__item {
		display: block;
	}
	.edu-subnav__item__a {
		display: block;
		padding: 2px $bl;
		color: #fff;

		&:hover {
			color: #fff;
			background: $color_red;
		}
	}
}

// DEPRECATED
// the global navigation of a single module
// place below a hero image, will be pulled up to sit on the hero image as tabs 
//
.edu-hero-nav {
	.edu-nav__item {
		white-space: nowrap;
		float: left;
	}
	.edu-nav__item__a {
		display: block;
		padding: 9px ($bl);
		text-transform: uppercase;
		font-weight: 500;
		transition: all .1s ease;
	}
	.edu-nav-item--active {
		.edu-nav__item__a {
			color: $color_text;
		}
	}
}

// a tabbed navigation
//
.edu-tab-nav {
	border-bottom: 1px solid $color_line;
	margin-bottom: ($bl*2);
	position: relative;
	.edu-nav__item {
		white-space: nowrap;
		float: left;
		margin-right: 3px;
	}
	.edu-nav__item__a {
		position: relative;
		margin-bottom: -1px;
		display: block;
		padding: 9px ($bl*2);
		font-weight: 500;
		transition: all .1s ease;
		border-bottom: 0;
		background: $color_fond;
		border-radius: 3px 3px 0 0;
		&:hover {
			background: #fff;
		}
	}
	.edu-nav-item--active {
		.edu-nav__item__a {
			color: $color_text;
			border: 1px solid $color_line;
			border-bottom: none;
			&:hover {
				background:  $color_fond;
			}
		}
	}
}

// a list of categories
//
.edu-cat-nav {

	.edu-nav__list {
		//border-top: 1px solid $color_line;
	}

	.edu-nav__item {
		//border-bottom: 1px solid $color_line;
	}

	.edu-nav__item__a {
		display: inline-block;
		//padding: ($bl/3) 0;

		&:hover {
			color: $color_red;
		}
	}

	.edu-nav-item--active {
		.edu-nav__item__a {
			color: $color_text;
		}
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 480
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: ($bp_s)) {
	.edu-h-nav {
		font-size: 14px;
	}
	.edu-hero-nav {
		margin: 0 (-$bl);
		padding: 0 9px;
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 480 < 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_s + 1)) and (max-width: $bp_m) {
	.edu-hero-nav {
		margin: 0 (-$bl*2);
		padding: 0 21px;
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT < 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: ($bp_m)) {
	.edu-hero-nav {
		border-bottom: 2px solid #fff;
		.edu-nav__item {
			margin-right: 4px;
		}
		.edu-nav__item__a {
			padding: 9px 6px;
			color: $color_link;
			&:hover {
				color: $color_red;
			}
		}
	}
}

//////////////////////////////////////////////////////////////////////////////
// LAYOUT > 768
//////////////////////////////////////////////////////////////////////////////
@media screen and (min-width: ($bp_m + 1)) {
	.edu-h-nav {
		font-size: 18px;
	}
	.edu-hero-nav {
		position: absolute;
		top: -36px;
		.edu-nav__item {
			margin-right: 4px;
		}
		.edu-nav__item__a {
			padding: 9px 12px;
			color: #fff;
			background: $color_link;
			line-height: 1;
			box-shadow: 0px -2px 2px 0px rgba($color_text, .2);
			&:hover {
				background: $color_text;
			}
		}
		.edu-nav-item--active {
			.edu-nav__item__a {
				background: $color_fond;
			}
		}
	}

	.edu-module-header-nav {
		.edu-subnav__list {
			top: 44px;
		}
		.edu-nav__item__a {
			padding: 10px $bl;
		}
	}
}
